import React, { useEffect } from "react";
import Skeleton from "react-loading-skeleton";
import styled from "styled-components";
import { get } from "lodash";
import { useMobile } from "#shared/hooks";
import AuthorDate, { AuthorDateWrapper } from "./AuthorDate";

const ArticleHero = ({ data, isArticlePagePending }) => {
    const skeletonHeight1 = useMobile(991) && "300px";
    const skeletonHeight2 = useMobile(768) && "191px";

    const ref = React.useRef(null);
    const [height, setHeight] = React.useState(0);

    React.useEffect(() => {
        setHeight(ref.current.clientHeight);
    }, [data]);

    const src = get(data, "heroImage.fields.file.url");
    const title = get(data, "postTitle");
    const author = get(data, "blogAuthor.fields.authorName");
    const date = get(data, "postDate");

    useEffect(() => {
        if (window.addthis) {
            window.addthis.update(
                "share",
                "url",
                "//s7.addthis.com/js/300/addthis_widget.js#pubid=ra-5e67e40eac34d36d",
            );
        }
    }, []);

    useEffect(() => {
        const script = document.createElement("script");
        script.src = "//s7.addthis.com/js/300/addthis_widget.js#pubid=ra-5e67e40eac34d36d";
        script.async = true;
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        };
    }, []);

    return (
        <Wrapper className="heroWrapper" height={height}>
            {isArticlePagePending ? (
                <Skeleton
                    style={{ height: skeletonHeight2 || skeletonHeight1 || "500px", width: "100%", display: "block" }}
                />
            ) : (
                <img className="heroImg" src={src} alt="hero" height="500" width="1028" />
            )}

            <div className="heroDescriptionsWrapper">
                <div className="heroDescriptions" ref={ref}>
                    <h1>
                        {isArticlePagePending ? (
                            <Skeleton style={{ height: "64px", width: "100%", display: "block" }} />
                        ) : (
                            title
                        )}
                    </h1>
                    {isArticlePagePending ? (
                        <Skeleton style={{ height: "20px", width: "60px", display: "block", marginTop: "15px" }} />
                    ) : (
                        <AuthorDate isBig author={author} date={date} full />
                    )}
                </div>
                <div className="socialsWrapper">
                    <Socials>
                        <div className="addthis_inline_share_toolbox" />
                    </Socials>
                </div>
            </div>
        </Wrapper>
    );
};

const Wrapper = styled.div`
    position: relative;

    ${AuthorDateWrapper} {
        color: #242e35;
        padding: 0;
        padding-top: 36px;

        @media screen and (max-width: 991px) {
            &.isBig {
                font-size: 12px;
                line-height: 16px;
                padding-top: 15px;
            }
        }
    }

    .heroImg {
        width: 100%;
        display: block;
        height: 500px;
        object-fit: cover;

        @media screen and (max-width: 991px) {
            height: 300px;
        }

        @media screen and (max-width: 768px) {
            height: 191px;
        }
    }

    .heroDescriptionsWrapper {
        margin: 0 20px;
        height: fit-content;
        position: relative;
    }
    .socialsWrapper,
    .heroDescriptions {
        max-width: 740px;
        margin: 0 auto;
    }

    .heroDescriptions {
        position: relative;
        margin: ${({ height }) => (height ? `-${height / 2}px` : "-180px")} auto 0;

        padding: 33px 50px;
        background: #ffffff;
        border: 2px solid #242e35;
        box-sizing: border-box;

        @media screen and (max-width: 991px) {
            margin: -41px auto 0;
            padding: 40px;
        }

        @media screen and (max-width: 600px) {
            padding: 20px;
        }

        h1 {
            font-family: RoslindaleDisplayCondensed;
            font-style: normal;
            font-weight: 500;
            font-size: 64px;
            line-height: 84px;
            margin: 0;
            color: #242e35;

            @media screen and (max-width: 768px) {
                font-size: 50px;
                line-height: 55px;
            }

            @media screen and (max-width: 600px) {
                font-size: 32px;
                line-height: 44px;
            }
        }
    }
`;

const Socials = styled.div`
    position: absolute;
    top: -105px;
    left: -71.85px;
    transform: translateX(-100%);
    display: flex;
    flex-direction: column;

    .at-share-btn-elements {
        display: flex;
        flex-direction: column;
    }

    a {
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 11px !important;
        border: 1px solid #a8abad !important;
        padding: 0px !important;
    }

    svg {
        width: 41px;
        height: 41px;
        padding: 10px;
    }

    @media screen and (max-width: 1340px) {
        margin-top: 27px;
        flex-direction: row;
        position: static;
        transform: none;

        .at-share-btn-elements {
            flex-direction: row;
        }

        a {
            margin-right: 11px !important;
            margin-bottom: 0 !important;
        }
    }
`;

export default ArticleHero;
